.price-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 323px;
  padding: 19px 68px 17px 68px;
  background-color: $color-second-black;
  border: 1px solid;
  border-image-slice: 1;
  border-image-source: $color-accent-main;

  @include vp-767 {
    padding: 19px 66px 17px 68px;
  }

  &__title {
    p {
      margin: 0;
      text-align: center;
      font-size: 14px;
      font-weight: 500;
      line-height: 20px;
      color: $color-light-grey;
    }
  }

  &__price {
    p {
      margin: 0;
      text-align: center;
      font-size: 60px;
      font-weight: 600;
      line-height: 66px;
    }
  }

  .btn {
    margin: 14px 0;
    user-select: none;

    @include vp-767 {
      min-width: 182px;
      justify-content: center;
      padding: 18px 34px;
    }
  }

  &__warranty {
    p {
      margin: 0;
      text-align: center;
      font-size: 12px;
      font-weight: 400;
      line-height: 20px;
      color: $color-spun-pearl;

      @include vp-767 {
        min-width: 171px;
      }
    }
  }
}
