.accordion {
  max-width: 548px;
  margin: 0 0 53px;

  @include vp-1023 {
    max-width: 100%;
    margin: 0 auto 50px;
  }

  @include vp-767 {
    margin: 0 auto;
  }

  & & {
    width: 90%;
    margin: 0 auto;
  }

  &__element {
    margin-bottom: 34px;

    svg {
      transition: transform $trans-default;
    }
  }

  &__button {
    width: 100%;
    padding: 0 0 12px;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: normal;
    text-align: left;
    text-transform: initial;
    background: transparent;
    user-select: none;
    transition: font-weight $trans-default;
  }

  &__element.is-active {
    svg {
      transform-origin: center;
      transform: rotate(180deg);
    }

    .accordion__button {
      font-weight: 500;
    }
  }

  &__wrapper {
    padding-top: 12px;

    @include vp-767 {
      padding-top: 12px;
    }
  }

  &__text {
    color: $color-light-grey;
  }
}
