.team-card {
  position: relative;
  width: 293px;
  height: 389px;
  padding: 9px;
  border: 1px solid;
  border-image-slice: 1;
  border-image-source: linear-gradient(90deg, rgba(255, 255, 255, 0.6), rgba(255, 255, 255, 0));
  background: rgba(37, 37, 50, 0.6);
  transition: border-image-source $trans-default;

  img {
    width: 100%;
    height: 100%;
    user-select: none;
  }

  &__inner {
    position: absolute;
    bottom: 17px;
    left: 50%;
    transform: translate(-50%, 0);
  }

  &__name,
  &__job-title {
    p {
      margin: 0;
      text-align: center;
      font-weight: 600;
    }
  }

  &__job-title {
    p {
      font-size: 12px;
      font-weight: 400;
      line-height: 20px;
      color: $color-spun-pearl;
    }
  }

  @include hover-focus {
    border-image-source: $color-accent-main;
  }
}
